@import "../../styles/styles.scss";
$color_white: #fff;
$color_blue: $color__primary;
$color_light-grey: #f6f6f6;
$color_grey: #2f4356;
$color_green: $color__success;
.contact {
    .hero {
        position: relative;
        padding-top: rem(100);
        background: linear-gradient(125.24deg, $color__primary 27.35%, #f2f9ff 176.96%);
        &__wrapper {
            position: relative;
            padding-top: rem(80);
            padding-right: rem(730);
            padding-bottom: rem(190);
            text-align: center;
            box-sizing: border-box;
            @include large {
                padding-right: 0;
                padding-bottom: 30%;
            }
            @include medium {
                padding-top: rem(10);
                padding-bottom: 80%;
            }
            @include small {
                padding-top: 0;
            }
        }
        &__name {
            margin: rem(20) 0;
            opacity: 50%;
            letter-spacing: rem(2);
            font-weight: 600;
            font-size: rem(14);
            text-transform: uppercase;
            @include medium {
                margin-top: rem(50);
                margin-bottom: rem(40);
            }
            @include small {
                margin-top: rem(50);
            }
        }
        &__content {
            padding: 0 $padding__collumn;
            text-align: left;
            color: $color_white;
            @include large {
                padding: 0 5%;
                text-align: center;
            }
            h1,
            p {
                max-width: 100%;
            }
        }
        &__title {
            font-size: rem(42);
            margin-bottom: rem(24);
            margin-top: 0;
            @include extraLarge {
                font-size: rem(38);
            }
            @include large {
                font-size: rem(34);
            }
            @include medium {
                font-size: rem(42);
            }
            @include small {
                font-size: rem(36);
            }
            @include extraSmall {
                font-size: rem(28);
            }
        }
        &__description {
            margin-bottom: rem(50);
            line-height: rem(25);
        }
        .form__box {
            position: absolute;
            top: rem(40);
            right: rem(156);
            width: rem(530);
            padding: rem(40);
            border-radius: rem(24);
            background: $color_white;
            box-shadow: 0px 20px 30px rgba(3, 26, 48, 0.08);
            box-sizing: border-box;
            text-align: left;
            z-index: 1001;
            @include large {
                top: 48%;
                right: 20%;
                width: 60%;
            }
            @include medium {
                top: 40%;
                right: 10%;
                width: 80%;
            }
            @include small {
                right: 5%;
                width: 90%;
            }
            &>form {
                &>label {
                    display: block;
                    margin-bottom: rem(20);
                    &:last-child {
                        margin-bottom: 0;
                    }
                    &>strong {
                        display: block;
                        margin-bottom: rem(6);
                    }
                    &>input {
                        display: block;
                        width: 100%;
                        height: rem(40);
                        padding: rem(16);
                        border: none;
                        border-radius: rem(8);
                        background: $color_light-grey;
                        font-size: rem(14);
                        box-sizing: border-box;
                    }
                    &>select,
                    &>textarea {
                        width: 100%;
                        padding: rem(16);
                        border: none;
                        border-radius: rem(8);
                        background: $color_light-grey;
                        font-size: rem(14);
                        box-sizing: border-box;
                    }
                    &>input:focus,
                    &>select:focus,
                    &>textarea:focus {
                        outline: 2px solid $color_green;
                    }
                }
                &>.button {
                    margin-top: rem(40);
                    @include small {
                        width: 100%;
                        margin: rem(40) 0 0;
                        padding: rem(15) 0;
                    }
                }
            }
        }
    }
    .help {
        position: relative;
        padding: rem(200) 0 rem(170);
        background: $color_white;
        @include large {
            padding-top: rem(400);
        }
        @include medium {
            padding-top: rem(150);
        }
        @include small {
            padding-top: rem(300);
        }
        &-name {
            display: block;
            padding: 0 $padding__collumn;
            font-size: rem(14);
            font-weight: bold;
            letter-spacing: 0.14em;
            text-align: center;
            text-transform: capitalize;
            color: $color_blue;
        }
        &-title {
            padding: 0 $padding__collumn;
            margin: rem(24) auto rem(54);
            font-size: rem(38);
            text-align: center;
            @include large {
                font-size: rem(34);
            }
            @include medium {
                font-size: rem(32);
            }
            @include small {
                font-size: rem(30);
            }
        }
        &__wrapper {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 0;
            @include small {
                grid-template-columns: repeat(1, 1fr);
            }
        }
        &__box {
            padding: $padding__collumn;
            &__inner {
                padding: rem(24);
                border-radius: rem(16);
                background: $color_light-grey;
            }
            &-title {
                display: block;
                position: relative;
                height: rem(50);
                padding: rem(12) 0 0 rem(70);
                font-size: rem(18);
                color: $color_grey;
                &::before {
                    display: inline-block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: rem(50);
                    height: rem(50);
                    border-radius: rem(50);
                    background-repeat: no-repeat;
                    background-position: center;
                    background-color: #ddeeff;
                    content: "";
                }
                @include medium {
                    font-size: rem(16);
                }
                @include small {
                    height: rem(32);
                }
            }
            &-desc {
                font-size: rem(16);
                margin: rem(16) 0;
                color: $color_grey;
            }
            &-link {
                display: inline-block;
                padding-right: rem(15);
                background: url("../../assets/images/ico_arrow_green.png") no-repeat right 60%;
                background-size: rem(5);
                font-size: rem(20);
                text-decoration: none;
                color: $color_green;
                &:hover {
                    transform: all 0.3s ease-out;
                    color: $color_blue;
                }
            }
            &:nth-child(1) {
                .help__box-title::before {
                    background-image: url("../../assets/images/icon-text.svg");
                }
            }
            &:nth-child(2) {
                .help__box-title::before {
                    background-image: url("../../assets/images/icon-email.svg");
                }
            }
            &:nth-child(3) {
                .help__box-title::before {
                    background-image: url("../../assets/images/icon-call.svg");
                }
            }
            &:nth-child(4) {
                .help__box-title::before {
                    background-image: url("../../assets/images/icon-chat.svg");
                }
            }
        }
    }
}